      '<b>Memórias do cárcere</b><br/><br/> Resolvo-me a contar, depois de muita hesitação, casos passados há dez anos − e, antes de começar, digo os motivos por que silenciei e por que me decido. Não conservo notas: algumas que tomei foram inutilizadas, e assim, com o decorrer do tempo, ia-me parecendo cada vez mais difícil, quase impossível, redigir esta narrativa. Além disso, julgando a matéria superior às minhas forças, esperei que outros mais aptos se ocupassem dela. Não vai aqui falsa modéstia, como adiante se verá. Também me afligiu a ideia de jogar no papel criaturas vivas, sem disfarces, com os nomes que têm no registro civil. Repugnava-me deformá-las, dar-lhes pseudônimo, fazer do livro uma espécie de romance; mas teria eu o direito de utilizá-las em história presumivelmente verdadeira? Que diriam elas se se vissem impressas, realizando atos esquecidos, repetindo palavras contestáveis e obliteradas?<br/><br/> (...)<br/><br/> O receio de cometer indiscrição exibindo em público pessoas que tiveram comigo convivência forçada já não me apoquenta. Muitos desses antigos companheiros distanciaram-se, apagaramse. Outros permaneceram junto a mim, ou vão reaparecendo ao cabo de longa ausência, alteramse, completam-se, avivam recordações meio confusas − e não vejo inconveniência em mostrá-los.<br/><br/> (...)<br/><br/> E aqui chego à última objeção que me impus. Não resguardei os apontamentos obtidos em largos dias e meses de observação: num momento de aperto fui obrigado a atirá-los na água. Certamente me irão fazer falta, mas terá sido uma perda irreparável? Quase me inclino a supor que foi bom privar-me desse material. Se ele existisse, ver-me-ia propenso a consultá-lo a cada instante, mortificar-me-ia por dizer com rigor a hora exata de uma partida, quantas demoradas tristezas se aqueciam ao sol pálido, em manhã de bruma, a cor das folhas que tombavam das árvores, num pátio branco, a forma dos montes verdes, tintos de luz, frases autênticas, gestos, gritos, gemidos. Mas que significa isso? Essas coisas verdadeiras podem não ser verossímeis. E se esmoreceram, deixá-las no esquecimento: valiam pouco, pelo menos imagino que valiam pouco. Outras, porém, conservaram-se, cresceram, associaram-se, e é inevitável mencionálas. Afirmarei que sejam absolutamente exatas? Leviandade. (...) Nesta reconstituição de fatos velhos, neste esmiuçamento, exponho o que notei, o que julgo ter notado. Outros devem possuir lembranças diversas. Não as contesto, mas espero que não recusem as minhas: conjugam-se, completam-se e me dão hoje impressão de realidade. Formamos um grupo muito complexo, que se desagregou. De repente nos surge a necessidade urgente de recompô-lo. Define-se o ambiente, as figuras se delineiam, vacilantes, ganham relevo, a ação começa. Com esforço desesperado arrancamos de cenas confusas alguns fragmentos. Dúvidas terríveis nos assaltam. De que modo reagiram os caracteres em determinadas circunstâncias? O ato que nos ocorre, nítido, irrecusável, terá sido realmente praticado? Não será incongruência? Certo a vida é cheia de incongruências, mas estaremos seguros de não nos havermos enganado? Nessas vacilações dolorosas, às vezes necessitamos confirmação, apelamos para reminiscências alheias, convencemo-nos de que a minúcia discrepante não é ilusão. Difícil é sabermos a causa dela, desenterrarmos pacientemente as condições que a determinaram. Como isso variava em excesso, era natural que variássemos também, apresentássemos falhas. Fiz o possível por entender aqueles homens, penetrar-lhes na alma, sentir as suas dores, admirar-lhes a relativa grandeza, enxergar nos seus defeitos a sombra dos meus defeitos. Foram apenas bons propósitos: devo ter-me revelado com frequência egoísta e mesquinho. E esse desabrochar de sentimentos maus era a pior tortura que nos podiam infligir naquele ano terrível.',
